* {   margin: 0;  padding: 0;   box-sizing: border-box; }

body { /* margin: 0; */ font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 
  'Droid Sans', 'Helvetica Neue', sans-serif; /* -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; */ background: #1f8a5b; color: #222; }

.booklistcss { width: 90vw; /* Relative to 1% of the width of the viewport where, viewport = the browser window size. If the viewport is 50cm wide, 1vw = 0.5cm. */
  max-width:fit-content; margin: 5rem auto; display: grid; gap: 2rem; /* rem =	Relative to font-size of the root element */ }

@media screen and (min-width: 768px) {
  .booklistcss {
    grid-template-columns: repeat(3, 1fr); /* specifies the number (and the widths) of columns in a grid layout. */
    align-items: flex-start;
  }
}

.bookcss {
  background: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
}

.bookcss h1{
  margin-top: 0.5rem;
}

